/*LoginPage*/

/*import React, { useState } from 'react';*/
import {useNavigate} from 'react-router-dom'; // useNavigate  
import {Label} from "./ui/label"
import {Input} from "./ui/input"
import {Button} from "./ui/button"
import axios from 'axios';
import React, {useEffect, useState} from 'react';

function Signup() {

    const navigate = useNavigate();
    const [username, setusername] = useState('');
    const [password, setPassword] = useState('');
    const [passwordCheck, setPasswordCheck] = useState('');
    const [email, setEmail] = useState('');
    const [Nick, setNick] = useState('');
    const [phone, setPhone] = useState('');
    const [passwordValid, setPasswordValid] = useState(false);

    useEffect(() => {
        const passwordRegex = /^(?=.*[a-z])(?=.*\d)(?=.*[!@#$%^&*])(?=.{8,20}$)/;
        setPasswordValid(passwordRegex.test(password));
    }, [password]);

    const validateForm = () => {
        //    
        if (!username || !password || !passwordCheck || !email || !phone) {
            alert('  .');
            return false;
        }
        //   
        if (password !== passwordCheck) {
            alert('  .');
            return false;
        }
        const passwordRegex = /^(?=.*[a-z])(?=.*\d)(?=.*[!@#$%^&*])(?=.{8,20})/;
        if (!passwordRegex.test(password)) {
            alert(' 8  20 , , ,      .');
            return false;
        }
        //   
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if (!emailRegex.test(email)) {
            alert('   .');
            return false;
        }
        //    (:    )
        const phoneRegex = /^[0-9\-]+$/;
        if (!phoneRegex.test(phone)) {
            alert('   .');
            return false;
        }
        return true;
    };

    const checkIdDuplicate = async () => {
        try {
            const response = await axios.post('http://localhost:8080/api/checkId', {
                //const response = await axios.post('http://13.125.161.122:8080/api/checkId', {
                username: username,
                nickname: Nick
            });
            if (response.data.isDuplicate) {
                alert(response.data.message);
                return true; //  ID 
            }
            return false; //  ID 
        } catch (error) {
            if (error.response && error.response.data.isDuplicate) {
                alert(error.response.data.message);
                console.log(error);
                return true;
            } else {
                alert(' .');
                return true;
            }
        }
    };

    const handleSignup = async () => {
        if (!validateForm()) return; //     
        const isDuplicate = await checkIdDuplicate(); // ID  
        if (isDuplicate) return; // ID    
        //    
        try {
            await axios.post('http://localhost:8080/api/Signup', {
                //await axios.post('http://13.125.161.122:8080/api/Signup', {
                username: username,
                password: password,
                nickname: Nick,
                email: email,
                phone: phone,
            });
            alert(' !');
            navigate('/'); //      
        } catch (error) {
            console.error(' ', error);
        }
    };

    return (
        <div className="flex flex-col gap-6 p-4 sm:p-6 items-center justify-center min-h-screen">
            <div className="absolute top-10 left-24">
                <Button onClick={() => navigate(-1)}></Button>
            </div>
            <div className="w-full max-w-[400px]">
                <div className="text-center py-6">
                    <h1 className="text-3xl font-bold"></h1>
                    <p className="text-gray-500 dark:text-gray-400"></p>
                </div>
                <div className="space-y-4">
                    <div className="space-y-2">
                        <Label htmlFor="username"></Label>
                        <Input id="username" placeholder=" ." value={username}
                               onChange={(e) => setusername(e.target.value)}/>
                    </div>

                    <div className="space-y-2">
                        <Label htmlFor="Nick"></Label>
                        <Input id="Nick" placeholder=" ." value={Nick}
                               onChange={(e) => setNick(e.target.value)}/>
                    </div>

                    <div className="space-y-2">
                        <Label htmlFor="password"></Label>
                        <Input id="password" type="password" placeholder=" ." value={password}
                               onChange={(e) => setPassword(e.target.value)}/>
                        <i className={`password-icon ${passwordValid ? 'text-green-500' : 'text-red-500'} text-sm mt-3`}>
                             8  20 , , ,      .
                        </i>
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="passwordck"> </Label>
                        <Input id="passwordck" type="password" placeholder=" ." value={passwordCheck}
                               onChange={(e) => setPasswordCheck(e.target.value)}/>
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="phone"> </Label>
                        <Input id="phone" value={phone} placeholder="'-'  ."
                               onChange={(e) => setPhone(e.target.value)}/>
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="email"></Label>
                        <Input id="email" type="email" placeholder=" ." value={email}
                               onChange={(e) => setEmail(e.target.value)}/>
                    </div>

                    <Button className="w-full" onClick={handleSignup}></Button>
                </div>
            </div>
        </div>
    );
}

export default Signup;