import {Avatar, AvatarImage} from "./avatar"
import {Link, useNavigate} from "react-router-dom";
import {useAuth} from '../../../contexts/AuthContext'; //      
import React, {useEffect, useState} from 'react';
import {BrowserView, MobileView} from "react-device-detect";
import BoardWrite from '../BoardWrite';
import Alarm from "./alarm.js";
import Setting from '../Setting.js'; // setting.js  import
import api from "../../../api";


// Define the Sidebar component
function Sidebar() {
    const navigate = useNavigate();
    const {logout} = useAuth();
    const [nickname, setNickname] = useState(''); //     
    const [img, setImg] = useState('');
    const [id, setId] = useState('');
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [alarmModal, setAlarmModal] = useState(false);
    const [settingModal, setSettingModal] = useState(false);
    const [newAlarm, setNewAlarm] = useState(false);
    useEffect(() => {
        // userInfo nickname   
        const userInfoJSON = localStorage.getItem('nickname');
        if (userInfoJSON) {
            const userInfo = JSON.parse(userInfoJSON);
            setNickname(userInfo.nickname); // nickname  
            setImg(userInfo.img);
            setId(userInfo.id);
        }
    }, []); //         
    useEffect(() => {
        const Alarm = async () => {
            try {
                // userId    
                const response = await api.get(`/CheckNewAlarm`, {
                    withCredentials: true,
                });
                setNewAlarm(response.data);
            } catch (error) {
                console.log(error);
            }
        };
        Alarm();
    }, []);

    const handleLogout = async () => {
        try {
            logout();
            navigate('/');
        } catch (error) {
            console.log(error);
            alert(' .');
        }
    };

    const openAlarmModal = () => setAlarmModal(true);
    const closeAlarmModal = () => setAlarmModal(false);

    const openModal = () => setIsModalOpen(true);
    const closeModal = () => setIsModalOpen(false);

    const openSettingModal = () => setSettingModal(true); //    
    const closeSettingModal = () => setSettingModal(false); //    

    return (
        <>
            <BrowserView>
                <div className="w-70 h-full bg-white p-5">
                    <div className="flex flex-col items-center">
                        <Avatar>
                            <div className="w-25 h-25 rounded-full overflow-hidden">
                                <AvatarImage alt="User Avatar" className="w-full h-full object-cover"
                                             src={img || "/placeholder.svg?height=100&width=100"}/>
                            </div>
                        </Avatar>
                        <h2 className="mt-4 font-semibold text-xl">{nickname}</h2>
                        <p className="text-sm text-gray-600">: 24  : 38</p>
                    </div>
                    <div className="mt-10 flex flex-col">
                        <Link className="text-gray-600 hover:bg-gray-200 p-2 rounded mb-2" to="/index"></Link>
                        <Link className="text-gray-600 hover:bg-gray-200 p-2 rounded mb-2"
                              onClick={openModal}></Link>
                        <Link className="text-gray-600 hover:bg-gray-200 p-2 rounded mb-2"
                              to={`/UserPage/${id}`}></Link>
                        <Link className="text-gray-600 hover:bg-gray-200 p-2 rounded mb-2" to="/Message"></Link>
                        <button
                            className={`p-2 mb-2 rounded flex justify-start items-center ${newAlarm ? 'text-red-600' : 'text-gray-600'} hover:bg-gray-200`}
                            onClick={() => {
                                setNewAlarm(false);
                                openAlarmModal();
                            }}
                        >
                            {newAlarm && <span className="w-2 h-2 bg-red-600 rounded-full mr-2"></span>}
                            
                        </button>
                        <Link className="text-gray-600 hover:bg-gray-200 p-2 rounded mb-2" onClick={openSettingModal}>
                            </Link>
                        <button className="text-gray-600 hover:bg-gray-200 p-2 mb-2 rounded flex justify-start"
                                onClick={handleLogout}>
                        </button>
                    </div>
                </div>
                <BoardWrite isOpen={isModalOpen} onRequestClose={closeModal} onClose={closeModal}/>
                {alarmModal && <Alarm isOpen={alarmModal} onClose={closeAlarmModal}/>}
                <Setting isOpen={settingModal} onRequestClose={closeSettingModal} onClose={closeSettingModal}/>
            </BrowserView>


            <MobileView>
                { /*      */}
                <div className="fixed bottom-0 left-0 w-full bg-white border-t border-gray-200 flex justify-around p-4"
                     style={{zIndex: '2'}}>
                    <Link className="text-gray-600 hover:bg-gray-200 p-2 rounded text-sm" to="/index"></Link>
                    <Link className="text-gray-600 hover:bg-gray-200 p-2 rounded text-sm" onClick={openModal}></Link>
                    <Link className="text-gray-600 hover:bg-gray-200 p-2 rounded text-sm"
                          to={`/UserPage/${id}`}></Link>
                    <Link className="text-gray-600 hover:bg-gray-200 p-2 rounded text-sm" to="/Message"></Link>
                    <Link className="text-gray-600 hover:bg-gray-200 p-2 rounded text-sm" onClick={openSettingModal}>
                        </Link>
                    <button className="text-gray-600 hover:bg-gray-200 p-2 rounded text-sm"
                            onClick={handleLogout}>
                    </button>
                </div>
                <BoardWrite isOpen={isModalOpen} onRequestClose={closeModal} onClose={closeModal}/>
                <Setting isOpen={settingModal} onRequestClose={closeSettingModal} onClose={closeSettingModal}/>
            </MobileView>
        </>
    );

}

// Export the Sidebar component
export default Sidebar;